.modalflex {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 50vh;
    width: 50vh;
}

.text {
    font-family: sans-serif;
    margin-bottom: 2vh;
    align-self: center;
}

.close {
    align-self: flex-end;
    background: 0px;
    border: 0px;
    font-size: 4vh;
    margin-bottom: auto;
}

.again {
    background-color: skyblue;
    width: 20vw;
    height: 6vh;
    font-size: 3vh;
    border: 0px;
}

.menu {
    background-color: lightcoral;
    width: 20vw;
    height: 6vh;
    font-size: 3vh;
    border: 0px;
}

.homebutton {
    width: 20vw;
    height: 6vh;
    font-size: 3vh;
    border: 0px;
    background-color: green;
    font-family: monospace;
    color: black;
}

.homebutton:hover {
    background-color: darkgreen;
}